import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { ShareIcon, HeartIcon, UserIcon } from '@heroicons/react/24/solid';
import Auth from '../utils/auth';

const Signup = () => {
    return (
        <div className="container p-5 h-full w-full">
            <div className='flex-col justify-center border-grey border-1 mb-10'>
                <h2 className='font-satisfy text-5xl text-slate text-center p-5'>Create an Account</h2>
                <div className='flex-col block justify-center'>
                    <form className='text-center font-mont-alt text-lg text-slate border-grey border-1'>
                        <div className='flex-row space-between p-2'>
                            <div>
                                <label htmlFor="username">Username:</label>
                            </div>
                            <div>
                                <input type='text' className="flex-1 m-1 form-input" name="username" id="username" placeholder='username'></input>
                            </div>
                        </div>
                        <div className='flex-row space-between p-2'>
                            <div>
                                <label htmlFor="email">Email:</label>
                            </div>
                            <div>
                                <input type='email' className="flex-1 m-1 form-input" name="email" id="email" placeholder='email'></input>
                            </div>
                        </div>
                        <div className='flex-row space-between p-2'>
                            <div>
                                <label htmlFor="password">Password:</label>
                            </div>
                            <div>
                                <input type='password' className="flex-1 m-1 form-input" name="password" id="pwd" placeholder='password'></input>
                            </div>
                        </div>
                        <div className="float-center p-5">
                            <button type="submit" className="p-2 px-4 bg-pink rounded-md font-satisfy text-center text-slate text-3xl" href="#">Create Account</button>
                        </div>
                    </form>
                </div>
                <div className='flex justify-center'>
                    <Link to="/login" className='text-center text-slate text-lg font-mont-alt'>← Go to Login</Link>
                </div>
            </div>

        </div>

    );
};

export default Signup;